#avatar_big {
    margin: 10px;
    width: 300px;
    height: 300px;
}
dialog {
    padding: 25px;
    background: dodgerblue;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.container{
    display:flex;
}
.wrapper{
    padding: 20px 0;
}
.container-column{
    flex-direction: column
}
.item-7{
    width: 65%;
}
.item-12{
    width:100%;
}
.container-buttons{
    position: relative;
    bottom:-40%;
}

.saveBtn + button, .saveBtn{
    font-size: 20px !important;    
    font-weight: 500;
    width: 205px;
    margin:10px;

}
.bntContainer{
    flex-direction: column;
    margin-right: 28px;
    align-items: center;
    justify-content: space-between;

}

.option-abs{
    position: relative;
    z-index: 1000;
}
